import React, { useState,useEffect } from 'react'
import { NavBar, Image,Form,Button,Input,TextArea,Toast } from 'antd-mobile'
import {
  useNavigate,
} from 'react-router-dom'
import axios from 'axios';
import Qs from 'qs'

import { useSelector } from 'react-redux';
import {
  getHomeList,
} from '../../store';
import './index.css'
import Api from '../../lib/Api';
export default () => {
	
	const [loading, setLoading] = useState(false)
	const {kefu} = useSelector(getHomeList);
	let navigate = useNavigate()
	useEffect(() => {
	},[])
  const back = () =>{
		navigate(-1);
	}
	const demoSrc2 = '/app/logo_top.png'
	const submit = function(values:any){
		setLoading(true)
		console.log(values);
		axios.post(Api.address()+'home/register', Qs.stringify(values))
		.then(function (response) {
			if(response.data.code == 0){
				Toast.show({
					icon: 'success',
					content: '',
				})
				localStorage.removeItem("nameAndPwd")
				setTimeout(()=>{
					navigate('/login/'+values.name)
				},1000)
			}else{
				
				Toast.show({
					icon: 'fail',
					content: response.data.msg,
				})
				setLoading(false)
			}
		})
		.catch(function (error) {
			Toast.show({
				icon: 'fail',
				content: '',
			})
			setLoading(false)
		})
	}

	return (
		<div className='App-main'>
			<header className="App-header"  >
      	<NavBar className='app-header' onBack={back}></NavBar>
			</header>
			<div className='App-content' style={{height:window.innerHeight-45,backgroundColor: "#fff"}}>
				<div className='login-img'>
					<Image lazy height={50} fit='scale-down' src={demoSrc2} />
				</div>
				<div>
					<Form
						onFinish = {submit}
						layout='horizontal'
						footer={
							<Button block loading={loading} type='submit' color='danger' size='large' style={{backgroundColor: "#e53333"}}>
								
							</Button>
						}
					>
						<Form.Item
							name='pid'
							label=''
							rules={[{ required: true, message: '' }]}
						>
							<Input placeholder='' />
						</Form.Item>
						<Form.Item
							name='name'
							label=''
							rules={[{ required: true, message: '' }]}
						>
							<Input  placeholder='' />
						</Form.Item>
						<Form.Item
							name='pwd'
							label=''
							rules={[{ required: true, message: '' }]}
						>
							<Input type='password' placeholder='' />
						</Form.Item>
						<Form.Item
							name='pwd2'
							label=''
							rules={[{ required: true, message: '' }]}
						>
							<Input type='password' placeholder='' />
						</Form.Item>
					</Form>

				</div>
				<div className='kefu'>
					
					<a href={kefu}></a>
				</div>
			</div>
		</div>
	)
}